import '../pages/rolePages/adminPage/adminPageStyle.css'

const Alert = ({
    editClientMode,
    setSelectedClient,
    selectedClient,
    handleDeleteClient,
    setAlertMode,
    setEditClienViewtMode
   
}) => {
    return (
        <div>
            {editClientMode && selectedClient && (
                <div style={{ display: "block" }} className={`user_modal_alert ${editClientMode && selectedClient ? "show" : ""}`}>
                    <div className="close" onClick={()=>setAlertMode(false)}>
                        &times;
                    </div>
                    <form>
                        <div className='alert_txt'>
                            <p>Подтвердите удаление лида</p>
                        </div>
                        <div className='btn_form_block'>
                            <button className='register' style={{ width: "120px" }} type="button" onClick={() => {handleDeleteClient(selectedClient._id); setAlertMode(false); setEditClienViewtMode(false)}}>
                                Подтвердить
                            </button>
                            <button className='register' style={{ width: "120px" }} type="button" onClick={()=>setAlertMode(false)}>
                                Отменить
                            </button>
                        </div>
                    </form>
                </div>
            )}
        </div>
    )
}

export default Alert